import { Inject, Injectable, PLATFORM_ID } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve } from '@angular/router';
import { Observable } from 'rxjs';
import { MetaTagInterface } from '../service/meta.service';
import { DetailProductDto, ProductService } from '@youtap-web-lib';
import { isPlatformServer } from '@angular/common';

@Injectable({ providedIn: 'root' })
export class DetailProductResolver implements Resolve<MetaTagInterface> {
  constructor(
    @Inject(PLATFORM_ID) private platformId: object,
    private productService: ProductService
  ) {}

  resolve(
    route: ActivatedRouteSnapshot
  ):
    | Observable<MetaTagInterface>
    | Promise<MetaTagInterface>
    | MetaTagInterface {
    if (isPlatformServer(this.platformId)) {
      const slug = route.params['slug'];
      const variantSlug = route.params['variantSlug'];
      const data: DetailProductDto = {
        detailProductInput: {
          slug: slug,
          ...(slug && { slug: `${slug}` }),
          ...(variantSlug && { productVariantSlug: `${variantSlug}` })
        },
        warehouseId: ''
      };
      return (
        this.productService
          .detailProduct(data)
          .toPromise()
          // eslint-disable-next-line @typescript-eslint/no-explicit-any
          .then(({ data }: any) => {
            // const meta: MetaTagInterface = {
            //   title: `Jual ${data.detailProduct.name} – ${data.detailProduct.productVariants[0].name} di ${data.detailProduct.supplier.name} | Youtap BOS`,
            //   description: `Beli ${data.detailProduct.name} ${data.detailProduct.productVariants[0].name} harga murah di ${data.detailProduct.supplier.name} hanya di Youtap BOS`,
            //   image: data.detailProduct.image
            // };
            return data;
          })
          .catch(() => {
            return {
              description: '',
              image: '',
              title: ''
            };
          })
      );
    } else {
      return {
        description: '',
        image: '',
        title: ''
      };
    }
  }
}
