/* eslint-disable @typescript-eslint/no-explicit-any */
import { Component, Inject, OnInit, PLATFORM_ID } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import {
  CurrencyPipe,
  isPlatformBrowser,
  isPlatformServer
} from '@angular/common';
import { Clipboard } from '@angular/cdk/clipboard';
import {
  trigger,
  state,
  style,
  transition,
  animate
} from '@angular/animations';

import {
  ProductService,
  CreateCartInputDto,
  CartService,
  ProductVariant,
  IsSupplierCoveredDto,
  SupplierService,
  ResponseDetailProductDto,
  DetailProductDto,
  ListProductDto,
  Products,
  getDetailLocationDto,
  LocationApiService
} from '@youtap-web-lib';

import { CountCartService } from '../../../shared/service/count-cart.service';
import { AuthData } from '../../../shared/data/auth.data';
import { MetaService } from '../../../shared/service/meta.service';
import { GaService } from '../../../shared/service/ga.service';
import { ReferralService } from '../../../shared/service/referral.service';
import { WindowActionService } from '../../../shared/service/window-action.service';
import { TransferStateService } from '../../../shared/service/transfer-state.service';

const STATE_KEY_PRODUCT = 'dataProduct';

@Component({
  selector: 'youtap-bos-product-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss'],
  animations: [
    trigger('toastAnimation', [
      state('void', style({ opacity: 0 })),
      state('*', style({ opacity: 1 })),
      transition(':enter, :leave', [animate('0.3s')])
    ])
  ]
})
export class DetailComponent implements OnInit {
  detailProduct!: ResponseDetailProductDto;
  loading = false;
  addLoading = false;
  page = 1;
  quantitySelect = 1;
  shareModal = false;
  link = '';
  message!: string;
  isPOD!: boolean;
  showTnc = false;
  token = '';
  isCovered = true;
  dataListProductMerge: Products[] = [];
  product_slug!: string | null;
  cartSuccess = false;
  productVariantSelected!: ProductVariant;
  priceBeforeDiscount = 0;
  isFullDescription = true;
  addressMap = '';
  variant_slug!: string | null;
  refCode!: string;
  showToast: boolean = false;
  variantSlugDetail!: string | null;
  isReady = false;
  promo: boolean = false;
  promoDiscount: boolean = false;
  stockOut: boolean = false;
  totalQuantityDiscount: number = 0;
  totalValueDiscount: string = '';
  warehouseId: string = '';

  public generateWhatsAppShareLink(
    product: string,
    supplier: string,
    link: string
  ): string {
    let shareLink: string;
    this.gaService.addEvent('Share Product Whatsapp ' + product, 'product', '');
    if (
      /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
        navigator.userAgent
      )
    ) {
      shareLink = `whatsapp://send?text=Beli ${product} di ${supplier} hanya di Youtap BOS ${link}`;
      window.open(shareLink, '_blank');
    } else {
      shareLink = `https://web.whatsapp.com/send?text=Beli ${product} di ${supplier} hanya di Youtap BOS ${link}`;
      window.open(shareLink, '_blank');
    }

    return shareLink;
  }

  constructor(
    @Inject(PLATFORM_ID) private platformId: object,
    private productService: ProductService,
    private route: ActivatedRoute,
    private router: Router,
    private cartService: CartService,
    private countCartService: CountCartService,
    private metaService: MetaService,
    private gaService: GaService,
    public authData: AuthData,
    private supplierService: SupplierService,
    private locationApiService: LocationApiService,
    private referralService: ReferralService,
    private clipboard: Clipboard,
    private windowActionService: WindowActionService,
    private currencyPipe: CurrencyPipe,
    private transferStateService: TransferStateService
  ) {}

  ngOnInit() {
    if (isPlatformBrowser(this.platformId)) {
      this.isReady = true;
    }
    const locationData = this.authData.location;
    if (locationData) {
      const { latitude, longitude } = locationData;
      this.getDetailLocation(latitude, longitude);
    }

    this.router.routeReuseStrategy.shouldReuseRoute = () => false;
    this.product_slug = this.route.snapshot.paramMap.get('slug') || '';
    this.variant_slug = this.route.snapshot.paramMap.get('variantSlug') || '';
    this.variantSlugDetail =
      this.route.snapshot.queryParams['variantSlugDetail'] || '';
    this.warehouseId = this.route.snapshot.queryParams['warehouseId'] || '';
    this.refCode = this.route.snapshot.queryParams['refcode'];
    this.getDetailProduct(this.product_slug, this.warehouseId);
    this.token = this.authData.token;
    this.link = this.windowActionService.getFullUrl();
    this.setReferral();
  }

  setReferral() {
    if (this.variant_slug && this.refCode) {
      this.referralService.setReferral(
        this.variant_slug,
        this.refCode,
        'product'
      );
    }
  }

  handleShowTnc() {
    this.showTnc = true;
  }

  fetchDataDetailProduct(isServer = false, slug: string, warehouseId: string) {
    const detailProductResolver = this.route.snapshot.data[
      'detailProduct'
    ] as ResponseDetailProductDto;
    const refCode = this.referralService.getReferral(
      detailProductResolver
        ? detailProductResolver.detailProduct?.supplier.slug
        : ''
    );
    const data: DetailProductDto = {
      detailProductInput: {
        ...(slug && { slug: `${slug}` }),
        ...(this.variant_slug && { productVariantSlug: `${this.variant_slug}` })
      },
      referralCode: refCode?.referral,
      warehouseId: warehouseId ? warehouseId : ''
    };
    this.productService.detailProduct(data).subscribe({
      next: ({ data }) => {
        this.metaService.setMeta({
          title: `Jual ${data.detailProduct.name} – ${data.detailProduct.productVariants[0].name} di ${data.detailProduct.supplier.name} | Youtap BOS`,
          description: `Beli ${data.detailProduct.name} murah di ${data.detailProduct.supplier.name} hanya di Youtap BOS`,
          image: data.detailProduct.image
        });

        this.loading = false;
        if (isServer) {
          this.transferStateService.set(STATE_KEY_PRODUCT, data);
        }
        const productVariant = data.detailProduct.productVariants
          .filter(
            (variant) =>
              variant.productVariantWarehouses &&
              variant.productVariantWarehouses[0].status === 'ACTIVE'
          )
          .map((val) => {
            val.isReferral = this.referralService.findReferral(val.slug)
              ? this.referralService.findReferral(val.slug)
              : val.isAffiliate;
            return val;
          });
        const prod: ResponseDetailProductDto = {
          detailProduct: {
            actualPriceMax: data.detailProduct.actualPriceMax,
            actualPriceMin: data.detailProduct.actualPriceMin,
            description: data.detailProduct.description,
            id: data.detailProduct.id,
            image: data.detailProduct.image,
            isActive: data.detailProduct.isActive,
            isPODAvailable: data.detailProduct.isPODAvailable,
            name: data.detailProduct.name,
            activeDiscount: data.detailProduct.activeDiscount,
            productVariants: productVariant.length
              ? productVariant
              : data.detailProduct.productVariants,
            slug: data.detailProduct.slug,
            supplier: data.detailProduct.supplier,
            warehouse: data.detailProduct.warehouse,
            categoryProducts: data.detailProduct.categoryProducts,
            isAvailable: productVariant.filter((stock) => stock.stock > 0)
              .length
              ? data.detailProduct.isAvailable
              : false
          }
        };
        this.detailProduct = prod;
        if (data.detailProduct.supplier && !isServer) {
          this.getListProduct(
            data.detailProduct.warehouse ? data.detailProduct.warehouse.id : ''
          );
          this.isSupplierCovered(data?.detailProduct?.supplier.id);
          this.reduceDiscountProductVariant(data.detailProduct.productVariants);
        }
        this.message = data.detailProduct.name;
        const variantHaveStock =
          this.detailProduct.detailProduct.productVariants.filter(
            (variant) => variant.stock > 0
          );
        this.checkProductStock(variantHaveStock);
        const haveVariant = variantHaveStock.find(
          (val) => val.slug === this.variantSlugDetail
        );
        const productVariantActive =
          this.detailProduct.detailProduct.productVariants.findIndex(
            (productVariant) =>
              variantHaveStock.length
                ? productVariant.productVariantWarehouses &&
                  productVariant.stock > 0 &&
                  productVariant.productVariantWarehouses[0].status === 'ACTIVE'
                : productVariant.productVariantWarehouses &&
                  productVariant.productVariantWarehouses[0].status === 'ACTIVE'
          );
        if (haveVariant) {
          this.productVariantSelected = haveVariant;
        } else {
          this.productVariantSelected =
            this.detailProduct.detailProduct.productVariants[
              productVariantActive === -1 ? 0 : productVariantActive
            ];
        }
        // const supplierReferral = this.referralService.findReferral(data.detailProduct.supplier.slug);
      },
      error: () => {
        this.loading = false;
      }
    });
  }

  getDetailProduct(slug: string, warehouseId: string) {
    this.loading = true;
    if (isPlatformServer(this.platformId)) {
      this.fetchDataDetailProduct(true, slug, warehouseId);
    }
    if (isPlatformBrowser(this.platformId)) {
      const data = this.transferStateService.get<ResponseDetailProductDto>(
        STATE_KEY_PRODUCT,
        {} as ResponseDetailProductDto
      );
      if (data && data.detailProduct) {
        this.loading = false;
        const productVariant = data.detailProduct.productVariants
          .filter(
            (variant) =>
              variant.productVariantWarehouses &&
              variant.productVariantWarehouses[0].status === 'ACTIVE'
          )
          .map((val) => {
            val.isReferral = this.referralService.findReferral(val.slug)
              ? this.referralService.findReferral(val.slug)
              : val.isAffiliate;
            return val;
          });
        const prod: ResponseDetailProductDto = {
          detailProduct: {
            actualPriceMax: data.detailProduct.actualPriceMax,
            actualPriceMin: data.detailProduct.actualPriceMin,
            description: data.detailProduct.description,
            id: data.detailProduct.id,
            image: data.detailProduct.image,
            isActive: data.detailProduct.isActive,
            isPODAvailable: data.detailProduct.isPODAvailable,
            name: data.detailProduct.name,
            activeDiscount: data.detailProduct.activeDiscount,
            productVariants: productVariant.length
              ? productVariant
              : data.detailProduct.productVariants,
            slug: data.detailProduct.slug,
            supplier: data.detailProduct.supplier,
            warehouse: data.detailProduct.warehouse,
            categoryProducts: data.detailProduct.categoryProducts,
            isAvailable: productVariant.filter((stock) => stock.stock > 0)
              .length
              ? data.detailProduct.isAvailable
              : false
          }
        };
        this.detailProduct = prod;
        if (data.detailProduct.supplier) {
          this.getListProduct(
            data.detailProduct.warehouse ? data.detailProduct.warehouse.id : ''
          );
          this.isSupplierCovered(data?.detailProduct?.supplier.id);
          this.reduceDiscountProductVariant(data.detailProduct.productVariants);
        }
        this.message = data.detailProduct.name;
        const variantHaveStock =
          this.detailProduct.detailProduct.productVariants.filter(
            (variant) => variant.stock > 0
          );
        this.checkProductStock(variantHaveStock);
        const haveVariant = variantHaveStock.find(
          (val) => val.slug === this.variantSlugDetail
        );
        const productVariantActive =
          this.detailProduct.detailProduct.productVariants.findIndex(
            (productVariant) =>
              variantHaveStock.length
                ? productVariant.productVariantWarehouses &&
                  productVariant.stock > 0 &&
                  productVariant.productVariantWarehouses[0].status === 'ACTIVE'
                : productVariant.productVariantWarehouses &&
                  productVariant.productVariantWarehouses[0].status === 'ACTIVE'
          );
        if (haveVariant) {
          this.productVariantSelected = haveVariant;
        } else {
          this.productVariantSelected =
            this.detailProduct.detailProduct.productVariants[
              productVariantActive === -1 ? 0 : productVariantActive
            ];
        }
      } else {
        this.fetchDataDetailProduct(false, slug, warehouseId);
      }
    }
  }

  checkProductStock(data: any) {
    if (data.length === 0) {
      this.stockOut = true;
    } else {
      this.stockOut = false;
    }
  }

  reduceDiscountProductVariant(productVariants: ProductVariant[]) {
    let totalQuantity: number = 0;
    let totalValue: string = '';
    let typeDiscount: string = '';

    productVariants.forEach((productVariant) => {
      if (
        productVariant.activeDiscounts &&
        productVariant.activeDiscounts.length > 0
      ) {
        // Get the first discount
        const discount = productVariant.activeDiscounts[0];

        totalQuantity += discount.minQuantity ? discount.minQuantity : 0;

        if (discount.type === 'FIXED') {
          const valueRupiah =
            this.currencyPipe.transform(discount.value, 'IDR', '', '1.0-0') ||
            '';
          totalValue += 'Rp' + this.formattedRupiah(valueRupiah) + ' ';
        } else if (discount.type === 'PERCENTAGE') {
          totalValue += discount.value + '% ';
        }

        // Set typeDiscount to the current discount type (this may be redundant if multiple types are expected)
        typeDiscount = discount.type ? discount.type : typeDiscount;
      }
    });

    // Assign the accumulated total values to the class properties
    this.totalQuantityDiscount = totalQuantity;
    this.totalValueDiscount = totalValue.trim(); // Trim any trailing spaces
  }

  formattedRupiah(value: string) {
    return value.replace(/,/g, '.');
  }

  isSupplierCovered(id: string) {
    if (this.token) {
      this.loading = true;
      const data: IsSupplierCoveredDto = {
        supplierId: id
      };
      this.supplierService.isSupplierCovered(data).subscribe({
        next: ({ data }) => {
          this.loading = false;
          this.isCovered = data?.isSupplierCovered;
        },
        error: (error) => {
          this.loading = false;
          return error;
        }
      });
    }
  }

  getListProduct(id: string) {
    this.loading = true;
    const data: ListProductDto = {
      listProductInput: {
        page: this.page,
        limit: 6,
        filter: {
          warehouseId: id,
          isAvailable: true,
          hasActiveDiscount: this.promo
        },
        order: {
          orderBy: 'UPDATED_AT',
          sortBy: 'ASC'
        }
      },
      warehouseId: id
    };
    this.productService.listProduct(data).subscribe({
      next: ({ data }) => {
        this.loading = false;
        this.dataListProductMerge = data.listProduct.products.filter(
          (val) => val.slug !== this.product_slug
        );
        if (this.dataListProductMerge.length > 5) {
          this.dataListProductMerge.splice(5, 1);
        }
        this.page += 1;
      },
      error: () => {
        this.loading = false;
      }
    });
  }

  changeQuantity(total: number) {
    this.quantitySelect = total;
  }

  goAddToCart() {
    if (this.authData.token) {
      // GA
      if (this.detailProduct.detailProduct.isPODAvailable) {
        this.gaService.addEvent('add cart POD', 'product', '');
      }
      const findReferral = this.referralService.getReferral(
        this.productVariantSelected.slug
      );
      const supplierReferral = this.referralService.getReferral(
        this.detailProduct.detailProduct.supplier.slug
      );
      this.addLoading = true;
      const data: CreateCartInputDto = {
        createCartInput: {
          productVariantWarehouseId:
            this.productVariantSelected.productVariantWarehouses[0].id,
          quantity: this.quantitySelect,
          referralCode: supplierReferral
            ? supplierReferral.referral
            : findReferral
            ? findReferral.referral
            : ''
        }
      };
      this.cartService.createCart(data).subscribe({
        next: () => {
          this.addLoading = false;
          this.cartSuccess = true;
          setTimeout(() => {
            this.cartSuccess = false;
          }, 2000);
          this.countCartService.getCountCart();
          this.referralService.useReferral(this.productVariantSelected.slug);
        },
        error: () => {
          this.addLoading = false;
        }
      });
    } else {
      this.router.navigate(['/login']);
    }
  }

  goDetailProduct(slug: string, warehouseId: string) {
    this.router
      .navigate([`product/detail/${slug}`], {
        queryParams: {
          warehouseId: warehouseId
        }
      })
      .then(() => {
        location.reload();
      });
  }

  goToDetailSupplier(slug: string) {
    this.router.navigate(['supplier/detail/'], {
      queryParams: {
        ss: slug
      }
    });
  }

  goToAddressList() {
    this.router.navigate(['/address/list']);
  }

  showModal() {
    this.shareModal = true;
  }

  getLink(product: string) {
    this.gaService.addEvent('Share Product Link ' + product, 'product', '');
    this.clipboard.copy(this.link);
    this.showToast = true;
    setTimeout(() => {
      this.showToast = false;
    }, 2000);
  }

  checkDiscount(productVariant: ProductVariant) {
    this.priceBeforeDiscount =
      productVariant.price + productVariant.discountAmount;
    if (this.priceBeforeDiscount > productVariant.price) {
      return true;
    } else {
      return false;
    }
  }

  getDetailLocation(lat: number, long: number) {
    const data: getDetailLocationDto = {
      detailLocationInput: {
        latitude: lat,
        longitude: long
      }
    };

    this.locationApiService.detailLocation(data).subscribe({
      next: ({ data }) => {
        this.addressMap = data.detailLocation.addressDetail;
      }
    });
  }
}
