import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { DetailComponent } from './detail/detail.component';
import {
  HomeNoFooterContainerComponent
} from '../../shared/layout/container/home-no-footer-container/home-no-footer-container.component';
import { CategoryProductComponent } from './category-product/category-product.component';
import { HomeContainerComponent } from '../../shared/layout/container/home-container/home-container.component';
import { DetailProductResolver } from '../../shared/resolvers/detail-product.resolver';
import { ProductCategoryResolver } from '../../shared/resolvers/product-category.resolver';

const routes: Routes = [
  {
    path: '',
    component: HomeNoFooterContainerComponent,
    data: { canGoBack: true },
    children: [
      {
        resolve: {
          detailProduct: DetailProductResolver
        },
        path: 'detail/:slug',
        component: DetailComponent,
      },
      {
        resolve: {
          meta: DetailProductResolver
        },
        path: 'variant-detail/:variantSlug',
        component: DetailComponent,
      }
    ]
  },
  {
    path: 'product-category',
    component: HomeContainerComponent,
    data: { canGoBack: true },
    children: [
      {
        resolve: {
          meta: ProductCategoryResolver
        },
        path: '',
        title: 'Kategori-Produk',
        component: CategoryProductComponent
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ProductRoutingModule {
}
