import { Inject, Injectable, PLATFORM_ID } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve } from '@angular/router';
import { Observable } from 'rxjs';
import { MetaTagInterface } from '../service/meta.service';
import { CategoryService, RequestDetailCategoryV2Dto } from '@youtap-web-lib';
import { isPlatformServer } from '@angular/common';

@Injectable({ providedIn: 'root' })
export class ProductCategoryResolver implements Resolve<MetaTagInterface> {
  constructor(
    @Inject(PLATFORM_ID) private platformId: object,
    private categoryService: CategoryService
  ) {}

  resolve(
    route: ActivatedRouteSnapshot
  ):
    | Observable<MetaTagInterface>
    | Promise<MetaTagInterface>
    | MetaTagInterface {
    const slug = route.queryParamMap.get('s') ?? '';
    const branchName = route.queryParamMap.get('branchName') ?? '';
    {
      if(isPlatformServer(this.platformId)){
        const data: RequestDetailCategoryV2Dto = {
          detailCategoryV2Input: {
            slug: slug
          }
        };

        return (
          this.categoryService
            .detailCategoryV2(data)
            .toPromise()
            // eslint-disable-next-line @typescript-eslint/no-explicit-any
            .then(({ data }: any) => {
              let meta: MetaTagInterface = {
                description: '',
                image: '',
                title: ''
              };
              if (branchName) {
                meta = {
                  title: `Jual ${branchName} Terlengkap Harga Murah | Youtap BOS`,
                  description: `Beli produk ${branchName} terlengkap harga murah dari supplier terpercaya hanya di Youtap BOS.`,
                  image: data.detailCategoryV2.image
                };
              } else {
                meta = {
                  title: `Jual Produk ${data.detailCategoryV2.name} Terlengkap Harga Murah | Youtap BOS`,
                  description: `Beli produk ${data.detailCategoryV2.name} terlengkap harga murah dari merek ternama dan supplier terpercaya. Belanja online kebutuhan usaha hanya di Youtap BOS.`,
                  image: data.detailCategoryV2.image
                };
              }
              return meta;
            })
            .catch(() => {
              return {
                description: '',
                image: '',
                title: ''
              };
            })
        );
      }
      else {
        return {
          description: '',
          image: '',
          title: ''
        };
      }
    }
  }
}
