import { Component, Inject, OnInit, PLATFORM_ID } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import {
  Category,
  CategoryService,
  ListCategoryDto,
  ListProductDto,
  ProductService,
  Products,
  RequestDetailCategoryV2Dto,
  ResponseListCategoryBranchDto,
  ImageSizeTypes,
  ResponseDetailCategoryV2Dto
} from '@youtap-web-lib';
import { ModalService } from '../../../shared/service/modal/modal.service';
import { PriceSortModalComponent } from '../../home/price-sort-modal/price-sort-modal.component';
import { CategoryModalComponent } from '../../home/category-modal/category-modal.component';
import { isPlatformBrowser, isPlatformServer, Location } from '@angular/common';
import { MetaService } from '../../../shared/service/meta.service';
import { TransferStateService } from '../../../shared/service/transfer-state.service';

const STATE_KEY_CATEGORY = 'dataCategory';

@Component({
  selector: 'category-product',
  templateUrl: './category-product.component.html'
})
export class CategoryProductComponent implements OnInit {
  id = '';
  slug = '';
  name = '';
  image = '';
  page = 1;
  loadingList = false;
  filterPriceSort = 'lowerPrice';
  nameLabel = 'Semua';
  categoryId!: string;
  categoryBranchData!: ResponseListCategoryBranchDto;
  listCategories: Category[] = [];
  categoryLength: boolean[] = [];
  loadingProductVariant = false;
  dataProducts: Products[] = [];
  canLoad = true;
  label: string = '';
  isEmptyState: boolean = false;

  constructor(
    @Inject(PLATFORM_ID) private platformId: object,
    private route: ActivatedRoute,
    private categoryService: CategoryService,
    private modalService: ModalService,
    private productService: ProductService,
    private location: Location,
    private router: Router,
    private metaService: MetaService,
    private transferStateService: TransferStateService
  ) {}

  ngOnInit(): void {
    this.slug = this.route.snapshot?.queryParamMap.get('s') ?? '';
    this.categoryId = this.route.snapshot?.queryParamMap.get('category') ?? '';
    if (this.categoryId) {
      this.getListVariant();
    }
    this.getDetailCategory();
    this.getListCategoryBranch();
  }

  fetchDataDetailCategory(isServer = false) {
    const data: RequestDetailCategoryV2Dto = {
      detailCategoryV2Input: {
        slug: this.slug
      }
    };
    this.categoryService.detailCategoryV2(data).subscribe({
      next: ({ data }) => {
        if(isServer) {
          this.transferStateService.set(STATE_KEY_CATEGORY, data);
        }
        if (!this.categoryId) {
          this.metaService.setMeta({
            title: `Jual Produk ${data.detailCategoryV2.name} Terlengkap Harga Murah | Youtap BOS`,
            description: `Beli produk ${data.detailCategoryV2.name} terlengkap harga murah dari merek ternama dan supplier terpercaya. Belanja online kebutuhan usaha hanya di Youtap BOS.`,
            image: data.detailCategoryV2.image
          });
        }

        this.name = data.detailCategoryV2.name;
        this.image = data.detailCategoryV2.image;
        this.id = data.detailCategoryV2.id;
        this.getListCategoryBranch();
        this.label = `Sabar yah! Kategori ${this.name} akan segera hadir di daerah Kamu!`;
      }
    });
  }

  getDetailCategory() {
    if(isPlatformServer(this.platformId)){
      this.fetchDataDetailCategory(true);
    }
    if(isPlatformBrowser(this.platformId)){
      const category = this.transferStateService.get<ResponseDetailCategoryV2Dto>(STATE_KEY_CATEGORY, {} as ResponseDetailCategoryV2Dto);
      if(category && category.detailCategoryV2) {
        this.name = category.detailCategoryV2.name;
        this.image = category.detailCategoryV2.image;
        this.id = category.detailCategoryV2.id;
        this.getListCategoryBranch();
      }
      else {
        this.fetchDataDetailCategory();
      }
      this.label = `Sabar yah! Kategori ${this.name} akan segera hadir di daerah Kamu!`;
    }
  }

  getListCategoryBranch() {
    try {
      this.loadingList = true;
      const data: ListCategoryDto = {
        listCategoryInput: {
          limit: 25,
          page: this.page,
          filter: {
            categoryParentId: this.id
          }
        }
      };

      this.categoryService.listCategoryBranch(data).subscribe({
        next: ({ data }) => {
          this.loadingList = false;
          data.listCategoryBranch.categories.forEach((listCategory) => {
            this.listCategories.push(listCategory);
            this.categoryLength.push(true);
          });

          const removeDuplicate = this.listCategories.filter(
            (item, index, self) =>
              index === self.findIndex((t) => t.id === item.id)
          );
          this.listCategories = removeDuplicate;

          if (this.categoryId) {
            this.nameLabel = data.listCategoryBranch.categories.filter(
              (val) => val.id === this.categoryId
            )[0].name;
            this.metaService.setMeta({
              title: `Jual ${this.nameLabel} Terlengkap Harga Murah | Youtap BOS`,
              description: `Beli produk ${this.nameLabel} terlengkap harga murah dari supplier terpercaya hanya di Youtap BOS.`,
              image: this.image
            });
          }
          if (!data.listCategoryBranch.categories.length) {
            this.canLoad = false;
          }
        },
        error: () => {
          this.loadingList = false;
        }
      });
    } catch (error) {
      this.loadingList = false;
    }
  }

  getListVariant() {
    this.loadingProductVariant = true;
    const data: ListProductDto = {
      listProductInput: {
        page: this.page,
        limit: 25,
        filter: {
          ...(this.categoryId && { categoryId: this.categoryId })
        },
        mutator: {
          imageResolution: ImageSizeTypes.M
        },
        order: {
          orderBy: 'PRICE',
          sortBy: this.filterPriceSort === 'lowerPrice' ? 'ASC' : 'DESC'
        }
      },
      warehouseId: ''
    };

    this.productService.listProduct(data).subscribe({
      next: ({ data }) => {
        this.loadingProductVariant = false;
        data.listProduct.products.forEach((val) => {
          const stockIsLess = val.productVariants.every(
            (variant) => variant.stock <= 0
          );
          if (!stockIsLess) {
            val.productVariants = val.productVariants.filter(
              (variant) => variant.stock > 0
            );
          }

          if (val.productVariants.length > 0) {
            this.dataProducts.push(val);
          }
        });
        //function showing empty state
        const threshold = this.dataProducts.length / 2;
        const isNotEmptyEnough = this.dataProducts.length > threshold;
        if (isNotEmptyEnough) {
          this.isEmptyState = false;
        } else {
          this.isEmptyState = true;
        }

        this.loadingProductVariant = false;

        if (!data.listProduct.products.length) {
          this.canLoad = false;
        }
      },
      error: () => {
        this.loadingProductVariant = false;
      }
    });
  }

  categorySort() {
    const modalRef = this.modalService.open(CategoryModalComponent, {
      title: 'Kategori',
      closeButton: true,
      data: {
        nameLabel: this.nameLabel,
        categoryList: this.listCategories
      }
    });
    modalRef.onResult().subscribe((data) => {
      this.page = 1;
      this.nameLabel = data.name;
      this.categoryId = data.id;
      this.listCategories = [];
      this.label = `Sabar yah! Kategori ${data.name} akan segera hadir di daerah Kamu!`;
      if (this.categoryId) {
        this.location.replaceState(
          `/product/product-category?s=${this.slug}&category=${this.categoryId}&branchName=${this.nameLabel}`
        );
        this.dataProducts = [];
        this.getListCategoryBranch();
        this.getListVariant();
      } else {
        this.getListCategoryBranch();
      }
    });
  }

  priceSort() {
    const modalRef = this.modalService.open(PriceSortModalComponent, {
      title: 'Urutkan',
      closeButton: true,
      data: {
        filter: this.filterPriceSort
      }
    });
    modalRef.onResult().subscribe(
      (data) => {
        this.page = 1;
        this.filterPriceSort = data;
        this.listCategories = [];
        if (this.categoryId) {
          this.dataProducts = [];
          this.getListVariant();
          this.getListCategoryBranch();
        } else {
          this.getListCategoryBranch();
        }
      },
      (dismissed) => {
        return dismissed;
      }
    );
  }

  haveProduct(length: number, index: number) {
    this.categoryLength[index] = length ? true : false;
    const haveLength = this.categoryLength.filter((val) => val);
    if (haveLength.length === 0) {
      this.isEmptyState = true;
    }
  }

  goProductDetail(slug: string, warehouseId: string) {
    this.router.navigate([`product/detail/${slug}`], {
      queryParams: {
        warehouseId: warehouseId
      }
    });
  }

  onScroll() {
    this.page = this.page + 1;
    if (this.canLoad) {
      if (this.categoryId) {
        this.getListVariant();
      } else {
        this.getListCategoryBranch();
      }
    }
  }

  loadMoreCategory(categoryId: string) {
    this.page = 1;
    this.categoryId = categoryId;
    this.location.replaceState(
      `/product/product-category?s=${this.slug}&category=${this.categoryId}`
    );
    this.getListCategoryBranch();
    this.getListVariant();
  }

  goBack() {
    this.location.back();
  }
}
