import { isPlatformBrowser, isPlatformServer } from '@angular/common';
import { Component, EventEmitter, Inject, Input, OnInit, Output, PLATFORM_ID } from '@angular/core';
import { Router } from '@angular/router';
import {
  ImageSizeTypes, ListProductDto, ProductService,
  ProductVariantOrderByTypes,
  ResponseListProductDto,
  ResponseListProductVariantDto,
  SortByTypes
} from "@youtap-web-lib";
import { TransferStateService } from '../../../service/transfer-state.service';
const STATE_KEY = 'listProduct';
@Component({
  selector: 'product-section',
  templateUrl: './product-section.component.html',
  styleUrls: ['./product-section.component.scss']
})
export class ProductSectionComponent implements OnInit {
  @Input() limit = 25;
  @Input() supplierId?: string;
  @Input() supplierSlug?: string;
  @Input() callBackMore = false;
  @Input() categoryId?: string;
  @Input() isAvailable?: boolean;
  @Input() villageId?: string;
  @Input() orderBy: ProductVariantOrderByTypes | string =
    ProductVariantOrderByTypes.NAME;
  @Input() sortBy: SortByTypes | string = SortByTypes.DESC;
  @Input() imageResolution = ImageSizeTypes;
  @Output() dataLength = new EventEmitter<number>();
  @Output() loadMore = new EventEmitter();
  @Input() warehouseId?: string;
  @Input() isFirstLoad = false;

  loadingProductVariant = false;
  promoDiscount: boolean = false;
  hasActiveDiscount: boolean = false;
  dataProductVariant!: ResponseListProductVariantDto;
  dataProduct!: ResponseListProductDto;

  constructor(
    @Inject(PLATFORM_ID) private platformId: object,
    private productService: ProductService,
    private router: Router,
    private transferStateService: TransferStateService
  ) { }

  ngOnInit(): void {
    if(this.isFirstLoad) {
      this.getListProductFirstLoad();
    }
    else {
      this.getListProduct();
    }
  }

  getListProduct(isServer = false) {
    this.loadingProductVariant = true;
    const data: ListProductDto = {
      listProductInput: {
        page: 1,
        limit: 6,
        filter: {
          ...(this.warehouseId && { warehouseId: this.warehouseId }),
          ...(this.categoryId && { categoryId: this.categoryId }),
          ...(this.isAvailable && { isAvailable: this.isAvailable }),
          ...(this.villageId && { villageId: this.villageId }),
          hasActiveDiscount: false,
        },
        mutator: {
          imageResolution: ImageSizeTypes.M,
        }
      },
      warehouseId: this.warehouseId ? this.warehouseId : '',
    };

    this.productService.listProduct(data).subscribe({
      next: ({ data }) => {
        this.loadingProductVariant = false;
        this.dataLength.emit(data.listProduct.products.length);
        this.dataProduct = data;
        if(isServer) {
          this.transferStateService.set(STATE_KEY, data);
        }
      }, error: () => {
        this.loadingProductVariant = false;
        this.dataLength.emit(0);
      }
    })
  }

  getListProductFirstLoad() {
    if(isPlatformServer(this.platformId)) {
      this.getListProduct(true);
    }
    if(isPlatformBrowser(this.platformId)){
     const data = this.transferStateService.get<ResponseListProductDto>(STATE_KEY, {} as ResponseListProductDto);
      if(data && data.listProduct) {
        this.dataProduct = data;
        this.dataLength.emit(this.dataProduct.listProduct.products.length);
      }
      else {
        this.getListProduct();
      }
    }
  }

  getProductDetail(slug: string, warehouseId: string) {
    this.router.navigate([`product/detail/${slug}`], {
      queryParams: {
        warehouseId: warehouseId
      }
    });
  }

  // getListVariant() {
  //   this.loadingProductVariant = true;
  //   const dataVariant: ListProductVariantDto = {
  //     listProductVarian: {
  //       page: 0,
  //       limit: 6,
  //       filter: {
  //         ...(this.supplierId && { supplierId: this.supplierId }),
  //         ...(this.categoryId && { categoryId: this.categoryId }),
  //         ...(this.isAvailable && { isAvailable: this.isAvailable }),
  //         ...(this.villageId && { villageId: this.villageId })
  //       },
  //       order: {
  //         orderBy: this.orderBy,
  //         sortBy: this.sortBy
  //       },
  //       mutator: {
  //         imageResolution: ImageSizeTypes.M
  //       }
  //     }
  //   };
  //   this.productService.listProductVariant(dataVariant).subscribe({
  //     next: ({ data }) => {
  //       this.dataLength.emit(data.listProductVariant.productVariants.length);
  //       this.dataProductVariant = data;
  //       this.loadingProductVariant = false;
  //     },
  //     error: () => {
  //       this.dataLength.emit(0);
  //       this.loadingProductVariant = false;
  //     }
  //   });
  // }

  goToDetailSupplier(slug?: string) {
    this.router.navigate(["/supplier/detail/"], {
      queryParams: {
        s: slug
      }
    });
  }
}
